import '../Profile.css';
import Nav from './Nav';

function Profile(){

    document.title = 'Profile'
    
    return(
        <>
        <Nav/>
        <div className="profile">
            <div className="name">Name: <span className="user-name">{localStorage.getItem('firstname')} {localStorage.getItem('lastname')}</span></div>
            <div className="username">Username: <span className="username">{localStorage.getItem('username')}</span></div>
            <div className="email">Email Adress: <span className="user-email"></span>{localStorage.getItem('useremail')}</div>
        </div>
        </>
    )
}

export default Profile;